<style scoped lang="less">
</style>
<template>
  <div class="addConsult content-layout">
    <div class="content-header">
      <div class="content-btn">
        <el-button type="info" size="small" @click="addConsult"></el-button>
      </div>
      <header-search :seachData="seachData" @seach="seachFn"></header-search>
    </div>
    <div class="content-main">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="a" label=""></el-table-column>
        <el-table-column prop="b" label=""></el-table-column>
        <el-table-column prop="c" label=""></el-table-column>
        <el-table-column prop="d" label=""></el-table-column>
        <el-table-column prop="e" label=""></el-table-column>
        <el-table-column prop="f" label=""></el-table-column>
        <el-table-column prop="g" label=""></el-table-column>
        <el-table-column prop="h" label="" width="150"></el-table-column>
        <el-table-column label="" width="200">
          <template scope="scope">
            <el-button type="text" size="small" @click.native.prevent="editconsult(scope.$index, tableData)"></el-button>
            <el-button type="text" size="small" @click.native.prevent="consultRecordSubmit(scope.$index, tableData)"></el-button>
            <el-button type="text" size="small" @click.native.prevent="consultRemove(scope.$index, tableData)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="prev, pager, next, jumper"
          :total="1000">
        </el-pagination>
      </div>
    </div>
    <transition name="el-fade-in">
      <addedit-record v-show="showRecord" :recordData="recordData"  @getChildMessage="closeRecord"></addedit-record>
    </transition>
  </div>
</template>
<script>
  import headerSearch from '../../../public/header-search'
  import addeditRecord from '../../../public/addeditRecord'
  import searchDataModule from '../../../../api/searchDataModule'
  import searchInitDefault from '../../../../api/searchInitDefault'
  export default {
    name: '',
    data(){
      return {
        seachData:  {
          starttime: searchDataModule.starttime,
          endtime: searchDataModule.endtime,
          xbval: searchDataModule.xbval,
          xb: searchDataModule.xb,
          seachInfo: searchDataModule.seachInfo
        },
        tableData: [
          {a: '00001', b: '', c: '', d: '90', e: '', f: '', g: '', h: ''},
          {a: '00001', b: '', c: '', d: '90', e: '', f: '', g: '', h: ''},
          {a: '00001', b: '', c: '', d: '90', e: '', f: '', g: '', h: ''},
          {a: '00001', b: '', c: '', d: '90', e: '', f: '', g: '', h: ''},
          {a: '00001', b: '', c: '', d: '90', e: '', f: '', g: '', h: ''},
          {a: '00001', b: '', c: '', d: '90', e: '', f: '', g: '', h: ''},
          {a: '00001', b: '', c: '', d: '90', e: '', f: '', g: '', h: ''},
          {a: '00001', b: '', c: '', d: '90', e: '', f: '', g: '', h: ''}
        ],
        currentPage: 1,
        recordData: {},
        showRecord: false,//
      }
    },
    props: ['a'],
    components: {
      headerSearch,
      addeditRecord
    },
    mounted(){
      searchInitDefault(searchDataModule);
    },
    methods: {
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
      },
      seachFn(val){
        console.log(this.seachData)
      },
      /*-------*/
      addConsult(){
        this.showRecord = true;
        this.recordData = {
          radio: ''
        }
      },
      editconsult(i,arr){
          /**/
        this.showRecord = true;
        this.recordData = {
          radio: '2'
        }
      },
      closeRecord(val) {
        //evtValue   
        this.showRecord = val
      },
      /**/
      consultRecordSubmit(i,arr){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
//          arr.splice(i, 1);
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      /**/
      consultRemove(i,arr){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          arr.splice(i, 1);
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      }
    }
  }
</script>



